'use client';

import React from 'react';
import { motion } from 'framer-motion';
import { useTranslations } from 'next-intl';
import { siteData } from '@/data/siteData';
import { ArrowRight, Clock, Star, ChevronDown, Mountain, Palmtree, Building2, MapPin } from 'lucide-react';
import Link from 'next/link';
import Image from 'next/image';
import { useSearchParams } from 'next/navigation';

interface PageProps {
    params: Promise<{
        locale: string;
    }>;
}

export default function ToursPage({ params }: PageProps) {
    const { locale } = React.use(params);
    const searchParams = useSearchParams();
    const categoryFilter = searchParams.get('category');

    const allTours = React.useMemo(() => {
        const categories = {
            'toubkal-treks': siteData.toubkalTreks,
            'desert-tours': siteData.desertTours,
            'imperial-cities': siteData.imperialCities,
            'excursions': siteData.excursions
        };

        if (categoryFilter && categories[categoryFilter as keyof typeof categories]) {
            return categories[categoryFilter as keyof typeof categories];
        }

        return [
            ...siteData.toubkalTreks,
            ...siteData.desertTours,
            ...siteData.imperialCities,
            ...siteData.excursions
        ];
    }, [categoryFilter]);

    const renderPrice = (item: any) => {
        if (item.pricing && item.pricing[0]) {
            const p = item.pricing[0] as any;
            return p.totalPrice ? `€${p.totalPrice}` : `€${p.pricePerPerson}`;
        }
        return 'Contact Us';
    };

    const getCategoryTitle = () => {
        switch (categoryFilter) {
            case 'toubkal-treks': return 'Toubkal Treks';
            case 'desert-tours': return 'Desert Adventures';
            case 'imperial-cities': return 'Imperial Cities';
            case 'excursions': return 'Day Excursions';
            default: return 'All Adventures';
        }
    };

    return (
        <div className="min-h-screen bg-surface-off">
            {/* Header Section */}
            <section className="relative h-[60vh] w-full flex items-center justify-center overflow-hidden">
                <div className="absolute inset-0">
                    <Image
                        src="/images/toubkal_treks/walking_toubkal_winter.jpg"
                        alt="Morocco Adventures"
                        fill
                        className="object-cover"
                        priority
                    />
                    <div className="absolute inset-0 bg-gradient-to-b from-black/60 via-black/20 to-surface-off" />
                </div>

                <div className="relative z-10 text-center px-6">
                    <motion.div
                        initial={{ opacity: 0, y: 30 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.8 }}
                    >
                        <span className="inline-block py-1.5 px-4 rounded-xl bg-primary/20 backdrop-blur-md text-primary-light text-sm font-bold uppercase tracking-widest mb-6 border border-primary/20">
                            Discover Morocco
                        </span>
                        <h1 className="text-5xl md:text-7xl lg:text-8xl font-bold text-white font-outfit mb-6 tracking-tight">
                            {getCategoryTitle()}
                        </h1>
                        <p className="text-white/80 text-xl md:text-2xl max-w-3xl mx-auto font-light leading-relaxed">
                            Handpicked local experiences from the High Atlas to the Sahara dunes.
                        </p>
                    </motion.div>
                </div>

                <motion.div
                    animate={{ y: [0, 10, 0] }}
                    transition={{ repeat: Infinity, duration: 2 }}
                    className="absolute bottom-12 left-1/2 -translate-x-1/2"
                >
                    <ChevronDown className="w-8 h-8 text-white/40" />
                </motion.div>
            </section>

            {/* Tours Grid */}
            <section className="py-24">
                <div className="container mx-auto px-6 md:px-4">
                    {/* Filter Pills */}
                    <div className="flex flex-wrap items-center justify-center gap-4 mb-20">
                        <Link
                            href={`/${locale}/tours`}
                            className={`px-8 py-3 rounded-2xl font-bold transition-all ${!categoryFilter ? 'bg-primary text-white shadow-xl shadow-primary/20' : 'bg-white text-neutral-medium hover:bg-neutral-50 border border-neutral-100'}`}
                        >
                            All
                        </Link>
                        {[
                            { id: 'toubkal-treks', label: 'Treks', icon: Mountain },
                            { id: 'desert-tours', label: 'Desert', icon: Palmtree },
                            { id: 'imperial-cities', label: 'Cities', icon: Building2 },
                            { id: 'excursions', label: 'Excursions', icon: MapPin }
                        ].map((cat) => (
                            <Link
                                key={cat.id}
                                href={`/${locale}/tours?category=${cat.id}`}
                                className={`flex items-center gap-2 px-8 py-3 rounded-2xl font-bold transition-all ${categoryFilter === cat.id ? 'bg-primary text-white shadow-xl shadow-primary/20' : 'bg-white text-neutral-medium hover:bg-neutral-50 border border-neutral-100'}`}
                            >
                                <cat.icon className="w-4 h-4" />
                                {cat.label}
                            </Link>
                        ))}
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-10">
                        {allTours.map((item: any, idx) => (
                            <motion.div
                                key={item.id}
                                initial={{ opacity: 0, y: 30 }}
                                whileInView={{ opacity: 1, y: 0 }}
                                viewport={{ once: true }}
                                transition={{ delay: idx % 3 * 0.1 }}
                            >
                                <Link href={`/${locale}/tours/${item.id}`} className="block group">
                                    <div className="bg-white rounded-[2rem] overflow-hidden shadow-sm hover:shadow-2xl transition-all duration-500 border border-neutral-100 h-full flex flex-col">
                                        <div className="relative h-[280px] overflow-hidden">
                                            <Image
                                                src={item.image || '/images/hero-marrakech.jpg'}
                                                alt={item.name}
                                                fill
                                                className="object-cover group-hover:scale-110 transition-transform duration-700"
                                            />
                                            {/* Top Badges */}
                                            <div className="absolute top-5 left-5 flex flex-col gap-2">
                                                <div className="bg-white/90 backdrop-blur-md px-4 py-2 rounded-xl text-xs font-bold flex items-center gap-2 shadow-lg">
                                                    <Clock className="w-4 h-4 text-primary" />
                                                    {item.duration?.replace('_', ' ')}
                                                </div>
                                            </div>
                                            <div className="absolute bottom-5 right-5">
                                                <div className="bg-primary px-4 py-2 rounded-xl text-white font-bold text-sm shadow-lg">
                                                    {renderPrice(item)}
                                                </div>
                                            </div>
                                        </div>

                                        <div className="p-8 flex-grow flex flex-col">
                                            <h3 className="text-2xl font-bold text-neutral-dark mb-4 font-outfit group-hover:text-primary transition-colors leading-tight line-clamp-2">
                                                {item.name}
                                            </h3>
                                            <p className="text-neutral-medium font-light text-base leading-relaxed mb-8 line-clamp-3">
                                                {item.description || "Discover the beauty of this authentic Moroccan experience with our expert team."}
                                            </p>

                                            <div className="mt-auto pt-6 border-t border-neutral-50 flex items-center justify-between">
                                                <span className="text-neutral-400 text-sm font-bold uppercase tracking-widest flex items-center gap-2">
                                                    <MapPin className="w-4 h-4" /> Morocco
                                                </span>
                                                <div className="w-12 h-12 rounded-2xl bg-neutral-50 flex items-center justify-center group-hover:bg-primary transition-colors duration-500">
                                                    <ArrowRight className="w-5 h-5 text-neutral-dark group-hover:text-white transition-transform group-hover:translate-x-1" />
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </Link>
                            </motion.div>
                        ))}
                    </div>

                    {allTours.length === 0 && (
                        <div className="text-center py-20">
                            <p className="text-neutral-400 text-xl font-light">No adventures found in this category yet.</p>
                            <Link href={`/${locale}/tours`} className="text-primary font-bold mt-4 inline-block">View all tours</Link>
                        </div>
                    )}
                </div>
            </section>
        </div>
    );
}
